<template>
  <div class="danmu-container">
    <div class="danmu-search-input">
      <el-input placeholder="ID" v-model="keyword" class="input-with-select">
        <!-- <el-button class="pre" slot="prepend" icon="el-icon-search"></el-button> -->
        <el-select v-model="selectType" slot="prepend" placeholder="">
          <!-- <el-option label="" value=""></el-option> -->
          <el-option label="" value="1"></el-option>
          <el-option label="ID" value="2"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select>
        <el-button class="suf" slot="append" @click="search"></el-button>
      </el-input>
    </div>
    <div class="date">
      <dl>
        <!-- <dt class="week-lable"><b></b></dt>
                <dd class="week">
                    <el-date-picker
                            v-model="date.week"
                            type="week"
                            format="yyyy  WW "
                            value-format="yyyy-MM-dd"
                            placeholder="">
                    </el-date-picker>
        </dd>-->
        <!-- <dt class="week-lable"><b></b></dt>
                <dd class="week">
                    <el-date-picker
                            v-model="date.month"
                            type="month"
                            format="yyyy-MM"
                            placeholder=""
                            @change="changeWeek">
                    </el-date-picker>
        </dd>-->
        <dt class="week-lable">
          <span style="font-size: 15px;"></span>
        </dt>
        <dd class="week">
          <el-select v-model="date.dateFrom" @change="selectDateChange" placeholder="">
            <el-option
              v-for="item in date.selectedDateRange"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </dd>
      </dl>
    </div>
    <el-table
      v-loading="tableLoading"
      element-loading-text=""
      element-loading-background="rgba(0, 0, 0, 0.8)"
      element-loading-custom-class="table-loading"
      :data="danmuPage.items"
      stripe
      style="width: 100%;color: #786c6c;"
    >
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="roomId" label="ID" width="100">
        <template slot-scope="scope">
          <span v-if="scope.row.roomId=='2267291'">17shou</span>
          <span v-else-if="scope.row.roomId=='4215314'">Mad98TnT</span>
          <span v-else-if="scope.row.roomId=='7163137'">17BaoLeii</span>
          <span v-else-if="scope.row.roomId=='7889732'">Smallccc</span>
          <span v-else-if="scope.row.roomId=='4074493'">yyxovo</span>
          <span v-else-if="scope.row.roomId=='2825872'"></span>
          <span v-else-if="scope.row.roomId=='218921'"></span>
          <span v-else-if="scope.row.roomId=='7099319'">SixGod</span>
          <span v-else-if="scope.row.roomId=='1589636'"></span>
          <span v-else-if="scope.row.roomId=='6481496'"></span>
          <span v-else-if="scope.row.roomId=='38698'">MSJOY</span>
          <span v-else-if="scope.row.roomId=='3168536'"></span>
          <span v-else-if="scope.row.roomId=='2448877'">Running</span>
          <span v-else-if="scope.row.roomId=='2461752'">47Gamer</span>
          <span v-else-if="scope.row.roomId=='8720038'"></span>
          <span v-else-if="scope.row.roomId=='276685'"></span>
          <span v-else-if="scope.row.roomId=='100'"></span>
          <span v-else-if="scope.row.roomId=='296059'"></span>
          <span v-else-if="scope.row.roomId=='6436883'">CRAZY112</span>
          <span v-else-if="scope.row.roomId=='5498522'"></span>
          <span v-else-if="scope.row.roomId=='2817923'">17Lanren</span>
          <span v-else-if="scope.row.roomId=='9302942'">SMGFlower</span>
          <span v-else-if="scope.row.roomId=='9303165'">SMG</span>
          <span v-else-if="scope.row.roomId=='6506031'">SMG</span>
          <span v-else-if="scope.row.roomId=='9302987'">Cloudglow</span>
          <span v-else-if="scope.row.roomId=='6297388'">17</span>
          <span v-else-if="scope.row.roomId=='5727911'">17</span>
          <span v-else-if="scope.row.roomId=='3530451'">737</span>
          <span v-else-if="scope.row.roomId=='6775876'">0v0</span>
          <span v-else-if="scope.row.roomId=='6013465'">LuL</span>
          <span v-else-if="scope.row.roomId=='8612090'">128</span>
          <span v-else-if="scope.row.roomId=='9169333'">DAQxxx</span>
          <span v-else>{{scope.row.roomId}}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="ownerName" label=""></el-table-column> -->
      <el-table-column prop="nn" label="" width="170"></el-table-column>
      <el-table-column prop="level" label="" width="80"></el-table-column>
      <el-table-column prop="bnn" label="" width="80"></el-table-column>
      <el-table-column prop="fansLevel" label="" width="80"></el-table-column>
      <el-table-column prop="urlev" label="" width="80"></el-table-column>
      <el-table-column label="" width="80">
        <template slot-scope="scope">
          <span v-if="scope.row.dat==1"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span
            class="txt"
            v-if="scope.row.ifs==1"
            v-bind:style="'color:'+tableColor[scope.row.col]+' ;'"
          >{{scope.row.txt}}</span>
          <span class="txt" v-else style="color: #333;">{{scope.row.txt}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createAt" label="" width="170"></el-table-column>
    </el-table>
    <el-pagination
      style="margin-top:20px;text-align:center;"
      :background="true"
      :page-size="danmuPage.limit"
      :pager-count="9"
      layout="prev, pager, next"
      :total="danmuPage.total"
      :current-page.sync="danmuPage.pageNum"
      @current-change="changePage"
    ></el-pagination>
  </div>
</template>

<script>
import utils from "@/utils/util";
import tableColor from "../config/tableColor.json";
export default {
  name: "Danmu",
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          var now = new Date();
          var day = now.getDate();
          now.setDate(day - 7);
          return time.getTime() > Date.now() || time.getTime() < now.getTime();
        },
      },
      danmuPage: {
        items: [],
        pageNum: 1,
        total: 0,
        offset: 0,
        limit: 100,
      },
      params: {
        pageNum: 1,
        limit: 100,
        startDate: null,
        endDate: null,
        ownerName: null,
        roomId: null,
        nn: null,
      },
      date: {
        selectedDateRange: [],
        dateFrom: null,
      },
      selectType: "3",
      keyword: null,
      tableLoading: false,
      tableColor: tableColor,
    };
  },
  created() {
    this.generateSelectDateRange();
    this.params.startDate = utils.formatDate(new Date(0), "yyyy-MM-dd");
    this.date.dateFrom = this.params.startDate;
    this.params.endDate = utils.formatDate(new Date(), "yyyy-MM-dd");
    var roomId = this.$route.query.roomId;
    if (roomId) {
      this.params.roomId = roomId;
      this.selectType = "2";
      this.keyword = roomId;
      this.$set(this.date, "dateFrom", this.params.startDate);
      this.getDanmuPage();
    }
  },
  methods: {
    getDanmuPage() {
      var $this = this;
      this.tableLoading = true;
      this.$request.getDanmuPage(this.params).then((res) => {
        $this.$set($this, "tableLoading", false),
          // $this.danmuPage = JSON.parse(JSON.stringify(res.body));
          console.log(res.body);
        $this.$set($this.danmuPage, "items", res.body.items);
        $this.$set($this.danmuPage, "pageNum", res.body.pageNum);
        $this.$set($this.danmuPage, "total", res.body.total);
        $this.$set($this.danmuPage, "offset", res.body.offset);
        $this.$set($this.danmuPage, "limit", res.body.limit);
      });
    },
    changePage(nextPage) {
      this.params.pageNum = nextPage;
      this.getDanmuPage();
    },
    search() {
      if (!this.selectType) {
        this.$message({
          message: "",
          type: "warning",
        });
        return;
      }
      if (!this.keyword) {
        var message = "";
        switch (this.selectType) {
          case "1":
            message = "";
            break;
          case "2":
            message = "ID";
            break;
          case "3":
            message = "";
            break;
          default:
        }
        this.$message({
          message: message,
          type: "warning",
        });
        return;
      } else {
        if (this.selectType === "2") {
          var reg = /^\d+$/g;
          if (!reg.test(this.keyword)) {
            this.$message({
              message: "",
              type: "error",
            });
            return;
          }
        }
      }
      this.$set(this.params, "ownerName", null);
      this.$set(this.params, "roomId", null);
      this.$set(this.params, "nn", null);
      switch (this.selectType) {
        case "1":
          this.params.ownerName = this.keyword;
          break;
        case "2":
          this.params.roomId = this.keyword;
          break;
        case "3":
          this.params.nn = this.keyword;
          break;
        default:
      }
      this.params.pageNum = 1;
      this.getDanmuPage();
    },
    generateSelectDateRange() {
      var now = new Date();
      var arr = new Array();
      arr[0] = { label: "", value: utils.formatDate(now, "yyyy-MM-dd") };
      for (var i = 1; i < 7; i++) {
        now.setDate(now.getDate() - 1);
        arr[i] = {
          label: `${i + 1}`,
          value: utils.formatDate(now, "yyyy-MM-dd"),
        };
      }
      arr[7] = {
        label: "",
        value: utils.formatDate(new Date(0), "yyyy-MM-dd"),
      };
      this.$set(this.date, "selectedDateRange", arr);
    },
    selectDateChange(selectedDate) {
      this.params.startDate = selectedDate;
      this.params.endDate = utils.formatDate(new Date(), "yyyy-MM-dd");
      if (!this.keyword) {
        this.$message({
          message: "",
          type: "warning",
        });
        return;
      }
      this.params.pageNum = 1;
      this.getDanmuPage();
    },
    // changeWeek(date){
    //     this.$set(this.date,'dateRange',null);
    //     if(date){
    //         var s = utils.formatDate(date,'yyyy-MM-dd');
    //         var d = new Date(date.getTime());
    //         d.setMonth(d.getMonth()+1);
    //         d.setDate(d.getDate()-1);
    //         var e = utils.formatDate(d,'yyyy-MM-dd');
    //         this.params.startDate = s;
    //         this.params.endDate = e;
    //     }else{
    //         this.$set(this.params,'startDate',null);
    //         this.$set(this.params,'endDate',null);
    //     }
    //     this.params.pageNum=1;
    //     this.getDanmuPage();
    // },
    // changeDateRange(dateRange){
    //     this.$set(this.date,'month',null);
    //     if(dateRange){
    //         var s = utils.formatDate(dateRange[0],'yyyy-MM-dd');
    //         var e = utils.formatDate(dateRange[1],'yyyy-MM-dd');
    //         this.params.startDate = s;
    //         this.params.endDate = e;
    //     }else{
    //         this.$set(this.params,'startDate',null);
    //         this.$set(this.params,'endDate',null);
    //     }
    //     this.params.pageNum=1;
    //     this.getDanmuPage();
    // }
  },
  mounted() {},
  updated() {
    console.log(this.selectType);
    console.log(this.date.dateFrom);
  },
};
</script>
<style>
.danmu-container .danmu-search-input {
  height: auto;
}
.danmu-container .danmu-search-input .el-input input {
  height: 56px;
}
.danmu-container .danmu-search-input .el-input .el-input-group__prepend {
  /* border-top-left-radius: 28px;
    border-bottom-left-radius: 28px; */
  background-color: #fff;
  width: 83px;
  padding: 0 15px;
}
.danmu-container .danmu-search-input .el-input .el-input-group__prepend i {
  font-size: 23px;
}
.danmu-container .danmu-search-input .el-input .el-input-group__append {
  border-bottom-right-radius: 28px;
  border-top-right-radius: 28px;
  padding: 0 46px;
  background: #3a8ee6;
  color: #ffffff;
  font-size: 17px;
  border: 1px;
}
.danmu-container .danmu-search-input .el-input .el-input-group__append span {
  letter-spacing: 7px;
}
.danmu-container .el-date-editor {
  width: 141px;
}
.danmu-container .el-date-editor.el-range-editor {
  width: 350px;
  height: 44px;
}
.danmu-container .el-date-editor.el-range-editor input {
  height: 38px;
}
.danmu-container .el-date-editor input {
  height: 44px;
  border: 0;
}
.danmu-container .el-pagination button:hover {
  color: #3a8ee6;
  background: #fff;
}
.danmu-container .el-pager li.active {
  color: #fff !important;
  background-color: #3a8ee6 !important;
}
.danmu-container .el-pager li:hover {
  color: #fff !important;
  background-color: #3a8ee6 !important;
}
.danmu-container .el-pagination.is-background .btn-prev {
  color: #3a8ee6;
  background-color: #fff;
}
.danmu-container .el-pagination.is-background .btn-prev:hover {
  color: #fff;
  background-color: #3a8ee6;
}
.danmu-container .el-pagination.is-background .btn-next {
  color: #3a8ee6;
  background-color: #fff;
}
.danmu-container .el-pagination.is-background .btn-next:hover {
  color: #fff;
  background-color: #3a8ee6;
}
.danmu-container
  .el-loading-mask.table-loading
  .el-loading-spinner
  .el-loading-text {
  color: #fff !important;
}
</style>

<style scoped>
@import url("../assets/css/danmu.css");
</style>





