import axios from 'axios'
import { Message } from 'element-ui'
// let baseUrl = 'http://127.0.0.1:8003/front';
// let baseUrl = 'http://117.51.157.106:8003/front';
let baseUrl = 'http://175.24.125.126:8003/front';
// if(location.hostname=='117.51.157.106'){
// 	baseUrl= 'https://117.51.157.106:8003/front';
// }
const service = axios.create({
    withCredentials: true,
    baseURL: baseUrl,
    timeout: 300000 // 
})

service.interceptors.request.use(function(config){
     // 
    return config;
},function(error){
    // 
    return Promise.reject(error);
})

// respone
service.interceptors.response.use(function(response){
    var res = response.data;
    if(res.errCode!=0){
        Message({
        message: res.errMsg,
        type: 'error',
        duration: 3 * 1000
      })
      return Promise.reject(response);
    }
    return res;
},function(error){
    console.log(error)
    Message({
    message: error.message,
    type: 'error',
    duration: 3 * 1000
    })
    return Promise.reject(error)
})
export default service;


